/* This CSS stylesheet is ordered by element > classes > IDs. */

/* Formatting elements */


body {
  font-family: "Lato", Arial, Helvetica, sans-serif;
  color: #2F323B;
}


p {
  font-weight: 400;
  line-height: 1.4;
}

anim {
  font-size: 600%;
  font-weight: 400;
  color:#2178db 
}

h1 {
  font-size: 220%;
  font-weight: 400;
}

h2 {
  font-size: 160%;
  font-weight: 400;
}

h3 {
  font-size: 120%;
}

a {
  font-weight: 700;
  text-decoration: none;
  color: #8E9AAF;
}

strong {
  font-weight: 600;
  color:#2178db 
}

bold {
  font-size: 160%;
  font-weight: bold;
}

em {
  color: #97989D;  
}

img {
  width: 100%;
  height: auto;
}

blockquote {
  padding: 0;
  margin: 48px 0;
  font-weight: 600;
  font-size: 125%;
  color: #8E9AAF;
}

